* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.section1 {
  padding: 80px 100px 0px 100px;
}

h1 {
  font-family: sans-serif;
  font-weight: 700;
  color: #fff;
  font-size: 50px;
  padding-bottom: 30px;
}

.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
  clear: both;
}

.card {
  padding: 60px 30px 60px 30px;
  height: 500px;
  width: 350px;
  float: left;
  background: #fff;
  border-radius: 2%;
  margin-left: 15px;
  margin-bottom: 45px;
}

.photo {
  border-radius: 50%;
}

h2 {
  padding-bottom: 20px;
  padding-top: 50px;
  font-size: 35px;
}

.feedback {
  font-size: 17px;
  padding-bottom: 25px;
}

.date {
  display: block;
  margin-top: 20px;
  font-size: 20px;
  color: grey;
  font-style: italic;
}